.App * {
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 20px;
  border-radius: 15px;
  margin: 1px;
  background-image: url(../Imagenes/fondo-natural-cielo-nubes.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  font-weight: 500;
}

.App {
  height: 100vh;
  display: grid;
  grid-column: 1fr 1fr;
}

