.Cargando,
.Colectivo {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
p,
.Valores {
  margin: 3px;
  padding: 10px;
}

p {
  font-weight: 500;
}

h2 {
  margin: 2px;
}

.ClimaPrincipal * {
  background: rgba(0, 0, 0, 0);
}

.ClimaSup {
  border: solid 2px black;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.Temperatura {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Valores,
.Titulo {
  border: solid 2px black;
  background: hsla(51, 100%, 81%, 0.5);
}

.Datos {
  display: flex;
}

.Temperatura,
.Datos {
  align-items: center;
  justify-content: space-evenly;
}

.DatosSol {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Titulo {
  margin: 10px 10px;
  align-self: stretch;
}

.DatosInf {
  display: grid;
  grid-template-rows: 1fr 4fr;
  justify-content: stretch;
}

.Imagen {
  background: #ffffffe6;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenOscura {
  background: #00000059;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenChicaClara {
  background: #ffffffe6;
  height: 50px;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenGrandeClara {
  background-color: #ffffffe6;
  height: 100px;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenChicaOscura {
  background: #00000059;
  height: 50px;
  border-radius: 45%;
  border: solid 1px black;
}
.Temperaturas {
  background: rgba(255, 255, 255, 0.45);
  border: solid 2px black;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: stretch;
}

.DatosInferiores {
  border: solid 2px black;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.DatosInferiores * {
  margin: 10px 10px;
}

.Colectivo,
.Cargando {
  border: solid 1px black;
  top: 50%;
  left: 50%;
}

.TextoPantalla {
  font-size: 300%;
}

.Simbolo {
  background-image: url(../Imagenes/FonsoSoleado4-transformed.jpeg);
}
